* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
  width: 100%;
  /* 浏览器默认 16px == 1rem, 这里设置为 10px, 那么子元素可以使用 rem 单位, 方便计算 */
  font-size: 10px;
} 

a {
  text-decoration: none;
}

body {
  background-color: #f3f3f3;
}

